  content: "";
  height: 100%;
  width: 32px;
  background: url("data:image/svg+xml;utf8,<svg fill='darkgray' height='30' viewBox='0 0 24 24' width='32' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/><path d='M0 0h24v24H0z' fill='none'/></svg>") 0 0 / contain no-repeat;
  transition: all .4s;
  position: absolute;
  top: 5px;
  right: 10px;
}

.select-box .options-container.active {
  max-height: 175px;
  opacity: 1;
  background: #fff3;
