a {
    text-decoration: none;
}
li * {
   pointer-events: none;
}

.events{
    position: relative;
        padding-left: 20px;
        padding-right: 20px;
    
        @media screen and (min-width: 768px) {
            width: 768px;
            margin-left: auto;
            margin-right: auto;
            padding-left: 32px;
            padding-right: 32px;
            }
            
        @media screen and (min-width: 1280px) {
            width: 1280px;
            padding-left: 16px;
            padding-right: 16px;
            }
}
.event_post{
    position: relative;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: -10px;
    
        @media screen and (min-width: 768px) {
            margin: -16px;
        }
    
        @media screen and (min-width: 1280px) {
            margin-left: -70px;
            margin-top: -38px;
            padding-bottom: 190px;
        }
}

.event_item {
    position: relative;
    display: block;
    z-index: 1;
    width: 130px;
    margin: 10px;
    transition-property: transform;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;

    &::before {
        content: url('../images/cards/square.svg');
        position: absolute;
        z-index: 1;
        width: 108px;
        height: 101px;
        top: -9px;
        right: 4px;

        transition-property: transform;
        transition-duration: 800ms;
        transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    }

    @media screen and (min-width: 768px) {
        //margin-left: 28px;
        //margin-top: 58px;
            flex-basis: calc(29% - 32px);
        margin: 16px;

        &::before {
            content: url('../images/cards/square-big.svg');
            width: 153px;
            height: 143px;
            top: -13px;
            right: -15px;
        }
    }

    @media screen and (min-width: 1280px) {
        margin-left: 70px;
        margin-top: 63px;
        margin-right: 0px;
        flex-basis: calc(21% - 83px);

        &::before {
                content: url('../images/cards/square-big.svg');
                width: 153px;
                height: 143px;
                top: -13px;
                right: 2px;
            }
    }
        &:hover::before,
        &:focus::before{
            transform: rotate(180deg);
        }
        &:hover,
        &:focus {
            transform: scale(1.1);
        }
        
       // &:hover~.event_item-image {
       //     box-shadow: 10px 10px 8px 2px rgba(233, 17, 175, 0.3);       
       //  }

        
    &-link {
        
        @media screen and (min-width: 1280px) {
        width: 180px;
        }
    }

    &-name {
        font-weight: 700;
        font-size: 16px;
        text-align: center;
        color: #dc56c5;
        margin-bottom: 5px;

        -ms-text-overflow: ellipsis;
        -o-text-overflow: ellipsis;
        text-overflow: ellipsis;
        overflow: hidden;
        -ms-line-clamp: 1;
        -webkit-line-clamp: 1;
        line-clamp: 1;
        display: -webkit-box;
        display: box;
        word-wrap: break-word;
        -webkit-box-orient: vertical;
        box-orient: vertical;
    }

    &-date {
        font-weight: 300;
        font-size: 14px;
        text-align: center;
        color: #ffffff;
        margin-bottom: 5px;
    }

    &-city {
        font-weight: 600;
        font-size: 14px;
        color: #ffffff;
        text-align: center;
        -ms-text-overflow: ellipsis;
        -o-text-overflow: ellipsis;
        text-overflow: ellipsis;
        overflow: hidden;
        -ms-line-clamp: 1;
        -webkit-line-clamp: 1;
        line-clamp: 1;
        display: -webkit-box;
        display: box;
        word-wrap: break-word;
        -webkit-box-orient: vertical;
        box-orient: vertical;
        &::before {
                content: url('../images/cards/localization.svg');
                width: 7px;
                height: 10px;
                margin-right: 3px;
            }
        }
    }
        .event_item-image {
            position: relative;
            z-index: 0;
            margin-bottom: 8px;
            display: block;
            width: 120px;
            height: 151px;
            border-radius: 50px 0;
            object-fit: cover;
         
            @media screen and (min-width: 768px) {
                    width: 180px;
                    height: 227px;
                }
            
            @media screen and (min-width: 1280px) {
                    width: 180px;
                    height: 227px;
                }
          // &:hover,
          //  &:focus{
          //     box-shadow: 10px 10px 8px 2px rgba(233, 17, 175, 0.3);
          //  }
        }